/*Strip the ul of padding and list styling*/
h1[data-v-91fbf6b6] {
  font-size: 3em;
  letter-spacing: 0.2rem;
  color: #999;
  padding: 0px 0px 10px 0px;
  margin:  0px 0px 50px 0px;
  border-bottom-style: solid;
  border-bottom-color: #999;
  border-bottom-width: 2px;
}
.clear[data-v-91fbf6b6] {
  float: clear;
}
nav[data-v-91fbf6b6] {
  display: grid;
}
ul[data-v-91fbf6b6] {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

/*Create a horizontal list with spacing*/
li[data-v-91fbf6b6] {
  display: inline-block;
  float: left;
  margin-right: 1px;
}

/*Style for menu links*/
li a[data-v-91fbf6b6] {
  display: block;
  margin-right: 20px;
  /* height: 10px; */
  text-align: center;
  color: #666;
  -webkit-text-decoration-line: none;
          text-decoration-line: none;
  padding: 5px;
}

/*Active color*/
li a.active[data-v-91fbf6b6] {
  color: #ff5035;
}

/*Hover state for top level links*/
li:hover a[data-v-91fbf6b6] {
  border-bottom-style: solid;
  border-bottom-color: #666;
  border-bottom-width: .5px;
}

/*Style 'show menu' label button and hide it by default*/
.show-menu[data-v-91fbf6b6] {
  text-decoration: none;
  color: #fff;
  background: #ff5035;
  text-align: center;
  padding: 10px 0;
  display: none;
}

/*Hide checkbox*/
input[type="checkbox"][data-v-91fbf6b6] {
  display: none;
}

/*Show menu when invisible checkbox is checked*/
input[type="checkbox"]:checked ~ #menu[data-v-91fbf6b6] {
  display: block;
}

